<template>
    <v-container style="width: 100%; " align="center">
        <v-app-bar
                fixed
                app
                color="#FFFFFF"
                elevation="3"
        >
            <v-row>
                <!--<v-col></v-col>-->
                <v-col class="text-center">
                    <v-toolbar-title>{{rankingText}}</v-toolbar-title>
                </v-col>
                <!-- <v-col class="text-right" style="padding-top: 0; padding-bottom: 0"><v-btn class="app-bar-button" small style="display: inline;" text color="primary"
                                                                                            @click="openProfileEdit">{{editText}}
                 </v-btn></v-col>-->
            </v-row>
        </v-app-bar>

        <v-row v-if="users.length > 0" dense style="margin-top: 54px">
            <div
                    style="width: 100%"
                    v-for="(user,index) in users"
                    :key="user.uid"
            >
                <v-card
                        style="padding: 5px"
                        :class="'user.uid == myUserID' ? 'my-ranking-card' : 'other-user-ranking-card'"
                        :color="'user.uid == myUserID' ? '#FFFFFF' : '#1976D2'"
                        @click="$router.push({path: '/dashboard/search/user/' + user.uid})">
                    <v-row>
                        <v-col cols="3">
                            <h2>{{index+1}}</h2>
                        </v-col>
                        <v-col>
                            <v-avatar style="width: 20%; padding-top: 20%; vertical-align: top; border-radius: 50%">
                                <img
                                        style="width: 100%; position: absolute; top: 0; height: 100%; object-position: center; object-fit: cover;"
                                        class="white--text align-end"
                                        :aspect-ratio="1"
                                        :src="user.profileImage"
                                >
                            </v-avatar>
                        </v-col>
                        <v-col>
                            <div>
                                <strong style="font-size: 12px">@{{user.username}}</strong>
                                <p>
                                    <v-icon color="yellow darken-3">mdi-star</v-icon>
                                    {{user.avgRating}}/5 ({{user.numRatings}})
                                </p>
                            </div>
                        </v-col>
                    </v-row>
                </v-card>
                <br>
            </div>
        </v-row>
        <v-row v-if="users.length == 0">
            <p>{{problemRetrievingRankingText}}</p>
        </v-row>
        <v-row>
            <v-col>

                <v-container>
                    <v-btn color="primary" @click="listRankingUsers(false)" v-if="!buttonIsDisabled"
                           :disabled="buttonIsDisabled"
                           class="bottom-centered-buttons">
                        {{watchMoreButtonText}}
                    </v-btn>
                </v-container>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
    import {mapActions, mapGetters, mapState} from 'vuex';
    import Constants from "../../store/Constants";

    export default {
        name: "RankingHome",
        data() {
            return {
                problemRetrievingRankingText: Constants.TEXT_PROBLEM_RETRIEVING_RANKING,
                watchMoreButtonText: Constants.TEXT_WATCH_MORE,
                buttonIsDisabled: true,
                rankingText: Constants.TEXT_RANKING
            }
        },
        methods: {
            ...mapActions([
                'listUsersRankingPageFirebase',
                'isNextRankingUsersPageEmptyFirebase',
            ]),
            listRankingUsers(isFirst) {
                let self = this;

                let resultListUsersRankingPageFirebase = this.listUsersRankingPageFirebase(isFirst);

                resultListUsersRankingPageFirebase
                    .then(() => {
                        self.isNextRankingUsersPageEmpty();
                    })
                    .catch((error) => {
                        self.buttonIsDisabled = true;

                        self.snackbarText = error;
                        self.snackbar = true;
                    });
            },
            isNextRankingUsersPageEmpty() {
                let self = this;

                let resultIsNextRankingUsersPageEmptyFirebase = this.isNextRankingUsersPageEmptyFirebase();

                resultIsNextRankingUsersPageEmptyFirebase
                    .then((isEmpty) => {
                        self.buttonIsDisabled = isEmpty;
                    })
                    .catch((error) => {
                        self.buttonIsDisabled = true;

                        self.snackbarText = error;
                        self.snackbar = true;
                    });
            },
        },
        mounted() {
            if (this.users.length == 0) {
                this.listRankingUsers(true);
            }
            else{
                this.isNextRankingUsersPageEmpty();
            }

        },
        computed: {
            ...mapGetters({
                users: 'getRankingUsers',
                myUserID: 'getUserInfoUID'
            })
        }
    }
</script>

<style scoped>
    .bottom-centered-buttons {
        left: 50%;
        transform: translate(-50%, 0);
    }

    .my-ranking-card {

    }

    .other-user-ranking-card {

    }
</style>